<template>
  <div id="course_container">
      <div class="course_top">
        <div class="course_title"> <p class="course_search"><van-icon name="search" v-on:click="onClick" /></p></div>
      <div class="course_down">
       <van-dropdown-menu>
          <van-dropdown-item title="" ref="ok">
          
                <div class="parent-box">
                    <p></p>
                  <ul class="tm-kind">
                    <li class="tmk-item" v-for="(item,index) in option1" :key="index">
                        <div>
                            <span
                            :class="sele == index?'active':''"
                            @click="bian(index)"
                            >{{item.name}}</span>
                        </div>
                    </li>
                  </ul>
                    <div >
                        <p></p>
                        <ul class="tm-kind">
                        <li class="tmk-item" v-for="(item,index) in option2" :key="index">
                        <div>
                            <span
                            :class="selee == index?'active':''"
                            @click="biann(index)"
                            >{{item.name}}</span>
                        </div>
                    </li>
                  </ul>
                    </div>
                    <div class="seach-btn">
                        <van-button  @click="chong"></van-button>
                        <van-button  @click="chon"></van-button>
                    </div>
                </div>
            </van-dropdown-item>
            <van-dropdown-item title="" ref="okk">
              <ul class="course-sort">
                <li 
                 v-for="(item,index) in paiList" :key="index"
                  @click="lolo(index)"
                  :class="lol == index?'active':''"
                 >
                  {{item.text}}
                </li>
              </ul>
                
            </van-dropdown-item>
             <van-dropdown-item title="" ref="okkk">
                  <div class="filter-box">
                    <div class="tm-kind">
                        <div class="tmk-item">
                            <span
                            
                            v-for="(item,index) in keList" :key="index"
                             @click="shai(index)"
                             :class="lole == index?'active':''"
                            >{{item.name}}</span>
                        </div>
                    </div>
                </div>
            </van-dropdown-item>
              
                  <van-list
                  
                    finished-text=""
                    v-model="loading"
                    :finished="finished"
                    @load="onLoad"
                  >
                    <van-cell>
                      <div class="course_list_container">
                        <div class="list_wrapper">
                          <div class="list_item_container" v-for="(item,index) in lists" :key="index" v-on:click="toDetails(item)">
                              <div class="list_item">
                                <div class="item">
                                    <div class="item_title">
                                    {{item.title}}
                                  </div>
                                  <div style="font-size:13px;color:gray;padding:10px 0px">

                                    {{item.start_play_date | times}}~{{item.end_play_date | times}}
                                    | {{item.total_periods}} 
                                  </div>
                                  <div class="item_img" v-for="(v,i) in item.teachers_list" :key="i">
                                      <img :src="v.teacher_avatar" alt="">
                                      <div>{{v.teacher_name}}</div>
                                  </div>
                                  <div class="item_bottom">
                                    <div>{{item.sales_num}}</div>
                                    <p>{{item.price | mian}}</p>
                                  </div>
                                </div>
                              </div>
                          </div>
                        </div>
                      </div>
                    </van-cell>
                  </van-list>
          <!-- <van-dropdown-item v-model="value2" :options="option2" /> -->
        </van-dropdown-menu>
      </div>
      </div>
      <div style="width:100%;height:100px"></div>
    
  </div>
</template>

<script>
export default {
  name: "KeCheng",
  data() {
    return {
      loading:false,
      finished:false,
      //
      courseInfo: {
        page: 1, //
        limit: 10, //
        course_type: 0, // 
        classify_id: "",
        order_by: "", //
        attr_val_id: "", //
        is_vip: 0 //vip
      },
      value1: 0,
      value2: "a",
      value3: "a",
      sele: -1,
      selee: -1,
      lol: 0,
      lole:0,
      lists: [],
      option1: [
        { id: 1, name: "" },
        { id: 2, name: "" },

        { id: 3, name: "" },

        { id: 4, name: "" },

        { id: 5, name: "" }
      ],
      option2: [
        { id: 7, name: "" },
        { id: 8, name: "" },
        { id: 9, name: "" },
        { id: 12, name: "" },
        { id: 13, name: "" }
      ],
      paiList: [
        { text: "", value: 0 },
        { text: "", value: 1 },
        { text: "", value: 2 },
        { text: "", value: 3 },
        { text: "", value: 4 }
      ],
      keList: []
    };
  },
  // - this
  created() {},
  filters: {
    times(value) {
      let time = new Date(value * 1000);
      let year = time.getFullYear();
      let month = time.getMonth() + 1;
      if (month < 10) {
        month = "0" + month;
      }
      let day = time.getDate() < 10 ? "0" + time.getDate() : time.getDate();
      let hour = time.getHours() < 10 ? "0" + time.getHours() : time.getHours();
      let minute =
        time.getMinutes() < 10 ? "0" + time.getMinutes() : time.getMinutes();
      let second =
        time.getSeconds() < 10 ? "0" + time.getSeconds() : time.getSeconds();
      return `${month}-${day} ${hour}:${minute}`;
    },

    mian(val) {
      if (val == 0) {
        return "";
      } else {
        return `${(val / 100).toFixed(2)}`;
      }
    }
  },
  // - DOM

  methods: {
    onClick(event) {
      // console.log(event)
      this.$router.push({
        name: "Search",
        params: {
          item: event
        }
      });
    },

    toDetails(value) {
      // console.log(value);
      this.$router.push({
        name: "Details"
      });
    },
    bian(index) {
      this.sele = index;
      // console.log(this.sele)
    },
    biann(index) {
      this.selee = index;
      // console.log(this.selee)
    },
    //
    chong() { 
      this.courseInfo.attr_val_id=""
      this.sele=-1
      this.selee=-1
       this.ajaxCourseList()
      this.$refs.ok.toggle();
    
    },
    //
    chon() { //
      
        let arr=[] //id
      // console.log(this.option1[this.sele].id)
      // console.log(this.option2[this.selee].id)
      if(this.sele != -1){
        let xueId = this.option1[this.sele].id
        arr.push(xueId)
      }
      if(this.selee !=-1){
        let banId= this.option2[this.selee].id
        arr.push(banId)
      }
      // console.log(arr)
      this.courseInfo.attr_val_id=arr.join(",")
      // console.log(this.courseInfo.attr_val_id)
      this.ajaxCourseList()
      // 
      this.$refs.ok.toggle();
    },
    //
    lolo(index) {
      this.lol = index;
      this.courseInfo.attr_val_id=index
       this.ajaxCourseList()
        this.$refs.okk.toggle();
      // console.log(this.courseInfo.attr_val_id)
      // console.log(this.lol)
    },
    //
    shai(index){
       this.lole = index;
      this.courseInfo.attr_val_id=index
       this.ajaxCourseList()
        this.$refs.okkk.toggle();
    },
    //
    async ajaxCourseList() {
      let { data: res } = await this.axios.get("api/app/courseBasis", {
        params: this.courseInfo
      });
      if (res.code != 200) {
        this.$toast(res.msg);
        return flase;
      }
      //  console.log(res.data)
      if(this.courseInfo.page !=1){
          this.lists = this.lists.concat(res.data.list)
      }else{
          this.lists = res.data.list;
      }
      this.loading=false
    },
    // 
    onLoad(){
      setTimeout(()=>{
          this.courseInfo.page++
          console.log(this.courseInfo.page)
          this.ajaxCourseList()
      },2000)
    }
  },
  mounted() {
    //  this.axios.get("api/app/myStudy/2").then((res)=>{
    //    console.log(res)
    //  })
    //  
    this.axios.get("api/app/courseClassify?").then(msg => {
      // console.log(msg)
      this.keList = msg.data.data.appCourseType;
      // console.log(this.option1);
    });
    //
    this.ajaxCourseList();
  }

  // reset(name) {                           //--
  //       this.$refs[name].toggle();
  //   searchFor(name) {                       //--
  //   },
  //       this.$refs[name].toggle();
  //       course(this.from).then(res => {
  //           res=res.data.list
  //           this.list=res;
  //       });
  //       this.search(this.fron)
  //   }
};
</script>
<style lang="scss" scoped>
/* @import url(); css */
.course_container {
  width: 100%;
}

.course_top {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.course_title {
  width: 100%;
  height: 46px;
  background: white;
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid gainsboro;
  position: relative;
}
.course-sort {
  background: #fff;
  li {
    height: 40px;
    text-align: center;
    line-height: 40px;
    font-size: 15px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(89, 89, 89, 1);
    border-bottom: 1px solid #f5f5f5;
    &:last-child {
      border: 0;
    }
  }
  li.active {
    color: #f77115;
  }
}
.course_search {
  width: 100px;
  height: 50%;
  position: absolute;
  right: 0px;
  top: 5px;
}

.course_down {
  width: 100%;
  height: 45px;
}

.course_list_container {
  width: 100%;
  height: 800px;
  background-color: rgb(240, 242, 245);
  overflow: scroll;
  margin-bottom: 50px;
}

.list_wrapper {
  width: 100%;
  /* height: ; */
}

.list_item_container {
  width: 100%;
  height: 200px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.list_item {
  width: 90%;
  height: 90%;
  padding: 5px;
  background-color: #fff;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
}

.item {
  width: 90%;
  height: 90%;
}

.item_img {
  width: 100%;
  height: 60px;
  display: inline-flex;
  align-items: center;
  border-bottom: 1px solid gainsboro;
  font-size: 14px;
  color: rgb(156, 156, 156);
}

.item_img img {
  width: 30px;
  border-radius: 30px;
  margin-right: 10px;
}

.item_bottom {
  width: 100%;
  height: 50px;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
}

.item_bottom div {
  font-size: 14px;
  color: gray;
}
.item_bottom p {
  font-size: 14px;
  color: rgb(11, 129, 11);
}
.parent-box {
  /* // padding-left: 30px; */
  padding-bottom: 20px;
  .seach-btn {
    padding: 10px 0px;
    display: flex;
    justify-content: space-between;
    button {
      width: 340px;
      height: 40px;
      background: rgba(255, 255, 255, 1);
      border-radius: 5px;
      border: 2px solid rgba(217, 217, 217, 1);
      font-size: 15px;
      font-family: PingFangSC-Regular;
      font-weight: 400;
      color: rgba(89, 89, 89, 1);
      margin-right: 10px;
      margin-left: 10px;
      &:last-child {
        color: #fff;
        // background: #C9CEF7;
        background: #eb6100;
        border: 0;
      }
    }
    span.active {
      color: #c9cef7;
      background: #ebeefe;
    }
  }
}
.tmk-item {
  span {
    margin: 5px 10px;
    display: inline-block;
    width: 74px;
    height: 35px;
    background: rgba(245, 245, 245, 1);
    border-radius: 6px;
    font-size: 15px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: #646464;
    text-align: center;
    line-height: 35px;
    overflow: hidden;
    &:nth-child(4n + 0) {
      margin-right: 0;
    }
  }
  span.active {
    color: red;
    // #ebeefe
    background: #ebeefe;
  }
}
.tm-kind {
  display: flex;
  padding-bottom: 20px;
  border-bottom: 1px solid #f1f1f1;
  flex-wrap: wrap;
  p {
    padding: 10px 10px;
    font-size: 13px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(89, 89, 89, 1);
  }
}
</style>